  background-color: #ffffff; }

/* arrow button styles */
.wp-block-button.is-style-dark-arrow .wp-block-button__link {
  border: 1px solid #000000; }

.wp-block-button.is-style-white-arrow .wp-block-button__link {
  border: 1px solid #ffffff; }

.wp-block-button.is-style-dark-arrow .wp-block-button__link,
.wp-block-button.is-style-white-arrow .wp-block-button__link {
  background-color: transparent;
  text-indent: -9999px;
  white-space: wrap;
  overflow: hidden;
  padding: 6px 20px;
  width: 38px;
  height: 26px;
  -webkit-transition: opacity .2s;
  transition: opacity .2s; }

.wp-block-button.is-style-white-arrow .wp-block-button__link {
  background: transparent url(../img/arrow-white.svg) center center no-repeat;
  background-size: 18px 12px; }

.wp-block-button.is-style-dark-arrow .wp-block-button__link {
  background: #ffffff url(../img/arrow-dark.svg) center center no-repeat;
  background-size: 18px 12px; }

.wp-block-button.is-style-dark-arrow .wp-block-button__link:hover,
.wp-block-button.is-style-white-arrow .wp-block-button__link:hover {
  opacity: .2; }

/* tags */
.i-tag,
.tags-links {
  font-size: 1.2em;
  letter-spacing: 0.01em;
  text-transform: uppercase;
  font-weight: normal;
  color: #333333; }

.tags-links a {
